import { graphql, Link, PageProps } from "gatsby";
import { MDXRenderer } from "gatsby-plugin-mdx";
import React from "react";
import { Helmet } from "react-helmet";
import Layout from "../components/common/Layout/Layout";
import {
  BioBody,
  BioHeader,
  BioPageContainer,
  Introduction,
  LinkedInLink,
  Name,
  OthersBio,
  OthersBioCards,
  Photo,
  Position,
  Socials,
  TeamMemberCardImage,
  TeamMemberCardName,
  TeamMemberCardTitle,
  TeamMemberCardWrapper,
  Title,
} from "../components/bio/styled";
import { GatsbyImage, IGatsbyImageData } from "gatsby-plugin-image";
import { Text } from "../components/common/Text";
import CustomLink from "../components/common/Link";
import SocialMediaButtons from "../components/common/SocialMediaButtons";
import { colors } from "../utils/colors";
import { mapBioNodeToTeamMemberInfo } from "../mappers";
import {
  GatsbySeoComponent,
  TopLevelPageJSONLDSchemas,
} from "../components/common/SEO";

const BioPage = ({ data }: PageProps<Queries.BioPageQuery>) => {
  const { position, photo, intro, linkedInLink, firstName, lastName } =
    data.mdx?.frontmatter!;

  const othersBio = data.allMdx.edges
    .filter((edge) => edge.node.id !== data.mdx?.id)
    .map((edge) => mapBioNodeToTeamMemberInfo(edge.node));

  const fullName = `${firstName} ${lastName}`;

  return (
    <>
      <Helmet title={`${fullName} | Telgorithm`}>
        <meta property="og:title" content={fullName} />
        <meta property="twitter:title" content={fullName} />
        <meta name="description" content={position as string}></meta>
        <meta property="og:description" content={position as string} />
        <meta property="twitter:description" content={position as string} />
        <meta
          property="og:image"
          content={
            data.mdx?.frontmatter?.photo?.childImageSharp?.gatsbyImageData
              .images.fallback?.src as string
          }
        />
        <meta
          name="twitter:image"
          content={
            data.mdx?.frontmatter?.photo?.childImageSharp?.gatsbyImageData
              .images.fallback?.src as string
          }
        />
      </Helmet>
      <TopLevelPageJSONLDSchemas />
      <GatsbySeoComponent
        canonicalUrl={"https://www.telgorithm.com/bios/" + data.mdx?.slug}
      />
      <Layout>
        <BioPageContainer>
          <BioHeader>
            <Photo>
              <GatsbyImage
                image={
                  photo?.childImageSharp?.gatsbyImageData as IGatsbyImageData
                }
                alt={fullName}
              />
            </Photo>
            <Title>
              <Name>
                <Text.Title.Bold.xl>Meet {fullName}</Text.Title.Bold.xl>
              </Name>
              <Position>
                <Text.Title.Bold.md>{position}</Text.Title.Bold.md>
              </Position>
            </Title>
            <Introduction>
              <Text.Regular.Normal.md>{intro}</Text.Regular.Normal.md>
            </Introduction>
          </BioHeader>
          <BioBody>
            <MDXRenderer children={data?.mdx?.body ?? ""} />
          </BioBody>
          <LinkedInLink>
            <CustomLink address={linkedInLink as string} arrow external>
              Click here to follow {firstName} on LinkedIn
            </CustomLink>
          </LinkedInLink>
          <OthersBio>
            <Text.Title.Bold.md>
              Meet the other Telgorithm Co-Founders.
            </Text.Title.Bold.md>
            <OthersBioCards>
              <Link to={othersBio[0].url}>
                <TeamMemberCardWrapper>
                  <TeamMemberCardImage>
                    <GatsbyImage
                      image={
                        othersBio[0].image?.childImageSharp
                          ?.gatsbyImageData as IGatsbyImageData
                      }
                      alt={othersBio[0].name}
                    />
                  </TeamMemberCardImage>
                  <TeamMemberCardName>{othersBio[0].name}</TeamMemberCardName>
                  <TeamMemberCardTitle>
                    {othersBio[0].jobTitle}
                  </TeamMemberCardTitle>
                </TeamMemberCardWrapper>
              </Link>
              <Link to={othersBio[1].url}>
                <TeamMemberCardWrapper>
                  <TeamMemberCardImage>
                    <GatsbyImage
                      image={
                        othersBio[1].image?.childImageSharp
                          ?.gatsbyImageData as IGatsbyImageData
                      }
                      alt={othersBio[1].name}
                    />
                  </TeamMemberCardImage>
                  <TeamMemberCardName>{othersBio[1].name}</TeamMemberCardName>
                  <TeamMemberCardTitle>
                    {othersBio[1].jobTitle}
                  </TeamMemberCardTitle>
                </TeamMemberCardWrapper>
              </Link>
            </OthersBioCards>
          </OthersBio>
          <Socials>
            <Text.Title.Bold.md>Discover us on social media</Text.Title.Bold.md>
            <SocialMediaButtons
              color={colors.primary.lightOrange}
              opacity={1}
            />
          </Socials>
        </BioPageContainer>
      </Layout>
    </>
  );
};

export default BioPage;

export const query = graphql`
  query BioPage($id: String) {
    mdx(id: { eq: $id }) {
      frontmatter {
        firstName
        lastName
        intro
        position
        linkedInLink
        photo {
          childImageSharp {
            gatsbyImageData
          }
        }
      }
      body
      id
      slug
    }
    allMdx(filter: { fileAbsolutePath: { regex: "//bios//" } }) {
      edges {
        node {
          frontmatter {
            lastName
            firstName
            intro
            position
            photo {
              childImageSharp {
                gatsbyImageData
              }
            }
          }
          id
          slug
        }
      }
    }
  }
`;
